<template>
    <div class="container">
      <h1>Entrada dos dados</h1>
      {{ sede }}
      <div class="form-group">
        <label for="sede">Sede:</label>
        <select id="sede" v-model="sede">
          <option
            v-for="(sede, index) in sedes"
            :key="index"
            :value="sede"
          >
            {{ sede.label }}
          </option>
        </select>
      </div>
  
      <div class="form-group">
        <label for="destino">EndereÃ§o de Destino:</label>
        <AddressAutoComplete @selectedPlace="handleSelectedPlace" />    
      </div>
  
      <div class="form-group">
        <label for="autonomia">Autonomia da Moto (L/km):</label>
        <input type="number" id="autonomia" v-model="autonomia" required>
      </div>
  
      <div class="form-group">
        <label for="capacidadeTanque">Capacidade do Tanque (L):</label>
        <input type="number" id="capacidadeTanque" v-model="capacidadeTanque" required>
      </div>

      <div>
        <button @click="buscarRotas">
          Buscar rotas
        </button>
      </div>

  
    </div>
  </template>
  
  <script>
  import AddressAutoComplete from './AddressAutoComplete.vue'
  import axios from 'axios';

  export default {
    name: 'MainComponent',

    components: {
      AddressAutoComplete
    },

    data() {
      return {
        sedes: [
          { label: 'Rio Claro - SP', value: "EitSLiA5IEEgLSBCZWxhIFZpc3RhLCBSaW8gQ2xhcm8gLSBTUCwgQnJhemlsIi4qLAoUChIJ9XCvJLjbx5QReMtVeX-JKxASFAoSCQto8chK2seUEe1S_v_Nw14l" },
          { label: 'Rio Claro - SP 2', value: "EitSLiA5IEEgLSBCZWxhIFZpc3RhLCBSaW8gQ2xhcm8gLSBTUCwgQnJhemlsIi4qLAoUChIJ9XCvJLjbx5QReMtVeX-JKxASFAoSCQto8chK2seUEe1S_v_Nw14l" }
        ],
        sede: {},
        destino: {},
        autonomia: null,
        capacidadeTanque: null,
      };
    },

    methods: {
      handleSelectedPlace(place) {
        console.log(place);
        this.destino = place;
      },

      buscarRotas() {
        console.log('buscar rotas')

        const variables = {
          objeto1: {
            id: this.sede?.value ?? '',
          },
          objeto2: {
            id: this.destino.place?.place_id ?? '',
            resume: this.destino.place?.name ?? '',
          },
        };

        console.log('JSON.stringify(variables): ', JSON.stringify(variables));

        axios.request({
          method: 'POST',
          url: 'https://olejeou6u5.execute-api.us-west-1.amazonaws.com/default/MotorRoutes_getRoutes',
          data: JSON.stringify(variables),
          headers: {
            // 'Access-Control-Allow-Origin': '*',
            // Accept: '*',
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
        })
        .then((res) => console.log(res))
        .catch((err) => console.log(err));
      }
    },
  };
  </script>
  
  <style>
  .container {
    padding: 20px;
    border: 1px solid #ccc;
    width: 500px;
    margin: 0 auto;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input,
  select {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
  }
  
  button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  </style>